@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
    margin: 0 auto;
    height: 100%;
    background: linear-gradient(180deg,  #8CE0EB 0%, #D2CCB1 100%);
}

.header {
    min-height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background: #D2CCB1;
    color: white;
    max-width: 1200px;
    margin: auto;

}

.header a {
    text-decoration: none;
    color: white;
}

.main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: calc(100vh - 60px);

}

.mainBlock{
    display: flex;
    flex-direction: row;
    background-color: white;
    margin: 30px;
    border-radius: 8px;
    box-shadow: 0px 4px 18px rgba(115, 146, 108, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    height: 100%;
    position: relative;
    max-width: 1200px;
    min-width: 600px;
    width: 100%;
}


@media only screen and (min-width: 375px) and (max-width: 900px) {
    .mainBlock {
        flex-direction: column;
    }
    .left__panel {
        flex-direction: row;
    }

}

.left__panel {
    min-width: 240px;
    padding: 25px;
    display: flex;
    flex-direction: column;
}

.center__panel {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    margin: 5px;
    position: relative;
}

.table__panel {
    min-height: 300px;
    height: 100%;
    min-width: 600px;
    width: 100%;
    padding-top: 20px;
}


.bottom__panel {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    padding-top: 30px;
    padding-bottom: 30px;
}
.MuiTableContainer-root {
    min-height: 300px;
    height: 100%;
    min-width: 600px;
    width: 100%;
}


.authBlock button {
    margin: 40px 0;
    border-radius: 40px;
    box-shadow: 0px 4px 18px rgba(33, 38, 143, 0.35), inset 0px 1px 0px rgba(255, 255, 255, 0.3);
}

.button__primary {
    background-color: chartreuse;
}


h2 {
    font-family: Poppins;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 33px;
    text-align: right;
    color: #2D2E46;
    word-wrap: break-word;
}

h4 {
    font-family: Poppins;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
    color: #2D2E46;
    opacity: 0.5;
}



